export interface Product {
  id: string
  name: string
  description: string
  overview?: string  // Added field for general overview content
  image: string
  images?: string[]  // Optional field for multiple images
  category: string
  subcategory: string
  features: string[]
  applications: string[]
  specifications?: { [key: string]: string }
  
  // D1 Database Schema - Additional Fields
  data_sheet?: string  // PDF/document link (snake_case for D1 compatibility)
  catalog_files?: string[]  // Catalog file list
  price?: string  // Price information
  is_warrantied?: boolean  // Warranty status
  has_free_shipping?: boolean  // Free shipping status
  is_featured?: boolean  // Featured product
  is_active?: boolean  // Active status
  display_order?: number  // Display order
  meta_title?: string  // SEO title
  meta_description?: string  // SEO description
  meta_keywords?: string  // SEO keywords
  
  // Legacy compatibility fields (camelCase versions)
  dataSheet?: string
  isWarrantied?: boolean
  hasFreeShipping?: boolean
  catalogFiles?: string[]
  
  // Timestamps
  created_at: string
  updated_at?: string  // Added field for last update timestamp
  
  // D1 Foreign Keys
  category_id?: string  // Category foreign key for D1
  subcategory_id?: string  // Subcategory foreign key for D1
}

export interface Subcategory {
  name: string
  key: string
  description: string
  products: Product[]
}

